<template>
  <div class="box">
    <div class="login-box">
      <div class="top-title">内蒙古自治区综合疾病预防控制中心</div>
      <div class="center">
        <div class="left">
          <img src="../assets/image/logos.png" alt />
          <div class="text">健康证件管理系统</div>
        </div>
        <div class="right">
          <h3>用户登录</h3>
          <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm">
            <el-form-item prop="username">
              <el-input placeholder="账号" v-model.number="ruleForm.username"></el-input>
            </el-form-item>
            <el-form-item prop="pass">
              <el-input type="password" @keyup.enter.native="submitForm('ruleForm')" placeholder="密码" v-model="ruleForm.pass" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" style="width:100%" @click="submitForm('ruleForm')">登录</el-button>
            </el-form-item>
          </el-form>
        </div>
      </div>
      <div class="footer">Copyright ©2018 booky, All Rights Reserved.</div>
    </div>
  </div>
</template>
<script>
const axios = require("axios");
export default {
  data() {
    var checkAge = (rule, value, callback) => {
      if (!value) {
        return callback(new Error("用户名不能为空"));
      }
      setTimeout(() => {
        if (!Number.isInteger(value)) {
          callback(new Error("密码不能为空"));
        }
      }, 1000);
    };
    var validatePass = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入用户名"));
      } else {
        if (this.ruleForm.checkPass !== "") {
          this.$refs.ruleForm.validateField("checkPass");
        }
        callback();
      }
    };
    var validatePass2 = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入密码"));
      } else {
        callback();
      }
    };
    return {
      ruleForm: {
        username: "",
        pass: ""
      },
      rules: {
        username: [
          {
            required: true,
            message: "用户名不能为空",
            trigger: "blur"
          }
        ],
        pass: [
          {
            required: true,
            message: "密码不能为空",
            trigger: "blur"
          }
        ]
      }
    };
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          axios({
            method: "post",
            url: "https://www.c65z45.cn/api/Reserve/Login",
            data: {
              Token: "B68DE9D1F8584D7099BCE47144D0351C",
              LoginID: this.ruleForm.username,
              Password: this.ruleForm.pass
            }
          })
            .then(res => {
              console.log(res);
              if (res.data.IsSuccess) {
                this.$message({
                  showClose: true,
                  message: "登陆成功",
                  type: "success"
                });
                sessionStorage.setItem(
                  "hospitalCookie",
                  "B68DE9D1F8584D7099BCE47144D0351C"
                );
                sessionStorage.setItem(
                  "hospitalData",
                  JSON.stringify(res.data.Data)
                );
                sessionStorage.setItem("loginId", this.ruleForm.username);
                this.$router.push({
                  name: "dashboard"
                });
              } else {
                this.$message({
                  showClose: true,
                  message: res.data.Message,
                  type: "error"
                });
              }
            })
            .catch(error => {
              this.$message({
                showClose: true,
                message: "服务器错误",
                type: "error"
              });
            });
        } else {
          this.$message({
            showClose: true,
            message: "请核对账号密码",
            type: "error"
          });
        }
      });
    }
  },
//   created() {
//     var _this = this;
//     document.onkeydown = function(e) {
//       let key = window.event.keyCode;
//       if (key == 13) {
//         _this.submitForm();
//       }
//     };
//   }
};
</script>
