import Vue from 'vue'
import Router from 'vue-router'
import Login from './views/Login.vue'

Vue.use(Router)

export default new Router({
	routes: [{
		path: '/',
		name: 'login',
		component: Login
	},
	{
		path: '/mains',
		name: 'mains',
		component: () => import('./views/Mains.vue'),
		meta: {
			title: '主页',
			isLogin: true
		},
		children: [{
			path: 'dashboard',
			name: 'dashboard',
			component: () => import('./views/Dashboard/Dashboard.vue'),
			meta: {
				title: '数据统计',
				icon: 'el-icon-yibiaopan1',
				isshow: true,
				isLogin: true
			}

		},
		{
			path: 'list',
			name: 'list',
			component: () => import('./views/List/Lists.vue'),
			meta: {
				title: '体检类型',
				icon: 'el-icon-lingyong',
				isshow: true,
				isLogin: true
			},
			children: [
				{
					path: 'healthy',
					name: 'healthy',
					component: () => import('./views/List/List.vue'),
					meta: {
						title: '健康证体检',
						icon: 'el-icon-zhengjian',
						isshow: true,
						isLogin: true
					}
				}, {
					path: 'aged',
					name: 'aged',
					component: () => import('./views/List/List1.vue'),
					meta: {
						title: '老年人体检',
						icon: 'el-icon-laoren',
						isshow: true,
						isLogin: true
					}

				}, {
					path: 'student',
					name: 'student',
					component: () => import('./views/List/List2.vue'),
					meta: {
						title: '学生体检',
						icon: 'el-icon-xuesheng',
						isshow: true,
						isLogin: true
					}

				}, {
					path: 'resident',
					name: 'resident',
					component: () => import('./views/List/List3.vue'),
					meta: {
						title: '居民体检',
						icon: 'el-icon-jumin',
						isshow: true,
						isLogin: true
					}

				}, {
					path: 'vaccine',
					name: 'vaccine',
					component: () => import('./views/List/List4.vue'),
					meta: {
						title: '疫苗接种',
						icon: 'el-icon-yimiao1',
						isshow: true,
						isLogin: true
					}

				}
			]
		},
		{
			path: 'time',
			name: 'time',
			component: () => import('./views/Time/Time.vue'),
			meta: {
				title: '日期设置',
				icon: 'el-icon-1',
				isshow: true,
				isLogin: true
			}
		},
		{
			path: 'input',
			name: 'input',
			component: () => import('./views/Input/Input.vue'),
			meta: {
				title: '录入结果',
				isshow: false,
				isLogin: true
			}
		},
		{
			path: 'password',
			name: 'password',
			component: () => import('./components/Password.vue'),
			meta: {
				title: '修改密码',
				isshow: false,
				isLogin: true
			}
		},
		{
			path: 'print',
			name: 'print',
			component: () => import('./components/Print.vue'),
			meta: {
				title: '打印',
				isshow: false,
				isLogin: true
			}
		}
		]
	}
	]
})
